<template>
  <div class="counter">
    <h4>Counter</h4>
    <p>{{ storeState.counter }}</p>
    <button @click="increment">+</button> <button @click="decrement">-</button>
  </div>
</template>

<script>
import { store } from "@/store.js";

export default {
  name: "Counter",
  data() {
    return {
      storeState: store.state
    };
  },
  methods: {
    increment() {
      store.commit("incrementCounter");
    },
    decrement() {
      store.commit("decrementCounter");
    }
  }
};
</script>

<style lang="scss" scoped>
p {
  color: #42b983;
}
</style>
